//! check every 2 second if slide changes, if question mark send it to frirebase
function monitorSlideIndexChanges() {
  let previousIndex = null;
  let isChecking = false;

  async function checkSlideIndex() {
    if (isChecking) return;

    isChecking = true;
    try {
      const currentIndex = await getSelectedSlideIndex();

      if (previousIndex !== null && currentIndex !== previousIndex) {
        // If there is a previously stored Firebase document, delete it
        if (firebaseDocId != null && fibrebaseTeacherId != null) {
          console.log("Deleting previous Firebase document...");
          await deleteFromFirebase(firebaseDocId, fibrebaseTeacherId);
          deleteAnswerCount(previousIndex);
          firebaseDocId = null;
          fibrebaseTeacherId = null;
        }
        console.log("hello");
        // Example call
         readTexts(); 
      }

      previousIndex = currentIndex;
    } catch (error) {
      console.error("Error checking slide index:", error);
    } finally {
      isChecking = false;
      // Schedule the next check
      setTimeout(checkSlideIndex, 500);
    }
  }

  // Start the first check
  checkSlideIndex();
}

