//! getting the index of the current slide (very important)
async function getSelectedSlideIndex() {
  try {
    return await new Promise((resolve, reject) => {
      Office.context.document.getSelectedDataAsync(Office.CoercionType.SlideRange, (asyncResult) => {
        if (asyncResult.status === Office.AsyncResultStatus.Failed) {
          reject(new Error(asyncResult.error.message));
        } else {
          resolve(asyncResult.value.slides[0].index);
        }
      });
    });
  } catch (error) {
    console.error("Error getting selected slide index:", error);
    throw error;
  }
}

