async function insertWhiteTextWithShape(textRecived) {
  await PowerPoint.run(async (context) => {
    // Get the index and ID of the currently selected slide
    let selectedSlideIndex = await getSelectedSlideIndex();
    const realSlideIndex = selectedSlideIndex - 1;
    const selectedSlide = context.presentation.slides.getItemAt(realSlideIndex).load("id");

    await context.sync(); // Sync to retrieve the slide ID

    // Options for inserting the slide
    const insertOptions = {
      formatting: "KeepSourceFormatting", // Keeps the formatting of the inserted slide
      targetSlideId: selectedSlide.id, // Insert the new slide after the currently selected one
    };

    // Insert the poll slide from the Base64 string
    context.presentation.insertSlidesFromBase64(pollText, insertOptions);
    await context.sync();

    const slide = context.presentation.slides.getItemAt(selectedSlideIndex);
    goToNextSlide();
    /*  const textRecived = 'abc123&0 Poll: Question?\n\nA) Red\nB) Blue\nC) Green\nD) Yellow'; */
    addTeacherIdentifier(slide, textRecived.substring(0, textRecived.indexOf(" ")));
    /* addQuestionText(slide, 'Question?'); */

    addPossibleAnswers(slide, textRecived.substring(textRecived.indexOf(" ") + 1));

    /*  addNumberOfStudnets("5"); */

    await context.sync();
  });
}

