function addPossibleAnswers(slide, text) {
  // Add the text box
  const textBox = slide.shapes.addTextBox(text, {
    left: 50,
    top: 50,
    width: 650,
    height: 200, // Increased height to accommodate more text
  });

  // Get the text frame of the shape
  const textFrame = textBox.textFrame;

  // Set the vertical alignment to top
  textFrame.verticalAlignment = PowerPoint.TextVerticalAlignment.top;

  // Get the text range to apply formatting
  const textRange = textFrame.textRange;

  // Set the overall text formatting
  textRange.font.size = 24; // Default font size
  /*  textRange.paragraphFormat.alignment = PowerPoint.ParagraphAlignment.left; */

  // Format the question (first line) to be bold and larger
  const questionRange = textRange.getSubstring(0, text.indexOf("\n"));
  questionRange.font.bold = true;
  questionRange.font.size = 30;
}

function addTeacherIdentifier(slide, text) {
  // Add a text box with white text
  const textBox = slide.shapes.addTextBox(text, {
    left: 10,
    top: 10,
    width: 200,
    height: 50,
  });
  const textRange = textBox.textFrame.textRange;
  textRange.font.color = "#FFFFFF";

  // Optionally, create a white rectangle to cover the text
  const shape = slide.shapes.addGeometricShape(PowerPoint.GeometricShapeType.rectangle, {
    left: 10,
    top: 10,
    width: 200,
    height: 50,
  });
  shape.fill.setSolidColor("#FFFFFF"); // Set shape fill to white
  shape.lineFormat.visible = false; // Make sure the shape has no border */
}

