async function createBarChartFromInput(realSlideIndex, inputStr) {
  try {
    await PowerPoint.run(async function (context) {
      // Get the slide by index and synchronize
      const slide = context.presentation.slides.getItemAt(realSlideIndex);
      slide.shapes.load("textFrame/textRange/text, textFrame/textRange/hasText, name");
      await context.sync(); // Sync to ensure the shapes are loaded

      // Check if a text box tagged as "answers received" already exists
      slide.shapes.items.forEach(function (shape) {
        console.log("the title is", shape.name);
        if (shape.name === "answername" || shape.name === "bargraph") {
          shape.delete();
        }
      });

      // Parse the input string (e.g., "A#1@B#3@C#5")
      const items = inputStr.split("@").map((item) => {
        const [label, count] = item.split("#");
        return { label, count: parseInt(count, 10) };
      });

      // Starting position for the bars and labels
      var topOffset = 50; // Starting y-coordinate
      const barHeight = 30; // Height of each bar
      const barSpacing = 15; // Space between bars
      const maxBarWidth = 500; // Maximum width for the largest bar

      // Define colors for the bars
      const barColors = ["#FF5733", "#33FF57", "#3357FF", "#FF33A6", "#A633FF"];

      // Iterate over the parsed items and create shapes for each one
      items.forEach((item, index) => {
        const { label, count } = item;

        // Add a shape as a "textbox" (rectangle with text)
        const textBoxx = slide.shapes.addTextBox(label + " [" + count.toString() + "]", {
          left: 50, // Adjust as needed
          top: topOffset, // Adjust as needed
          width: 400, // Adjust as needed
          height: 100, // Adjust as needed
        });
        textBoxx.name = "answername";

        // Calculate the width of the bar proportional to the count
        const barWidth = (count / Math.max(...items.map((i) => i.count))) * maxBarWidth;

        // Add the bar (rectangle) next to the label
        topOffset += barHeight + barSpacing;

        const barShape = slide.shapes.addGeometricShape(PowerPoint.GeometricShapeType.rectangle);
        barShape.left = 70; // Position next to the label
        barShape.top = topOffset;
        barShape.width = barWidth;
        barShape.height = barHeight;
        barShape.fill.setSolidColor("#008080" /* barColors[index % barColors.length] */); // Set bar color
        barShape.lineFormat.visible = false; // Hide bar border
        barShape.name = "bargraph";

        // Move the topOffset down for the next label/bar pair
        topOffset += barHeight + barSpacing;
      });

      // Synchronize to apply all changes to the slide
      await context.sync();
    });
  } catch (e) {
    console.log("error recieved", e);
  }
}

async function createWordCloudFromInput(realSlideIndex, inputStr) {
  try {
    await PowerPoint.run(async function (context) {
      // Get the slide by index and synchronize
      const slide = context.presentation.slides.getItemAt(realSlideIndex);
      slide.shapes.load("textFrame/textRange/text, textFrame/textRange/hasText, name");
      await context.sync(); // Sync to ensure the shapes are loaded

      // Remove any existing shapes with the name 'answername' or 'wordcloud'
      slide.shapes.items.forEach(function (shape) {
        if (shape.name === "answername" || shape.name === "wordcloud") {
          shape.delete();
        }
      });

      // Parse the input string (e.g., "A#1@B#3@C#5")
      const items = inputStr.split("@").map((item) => {
        const [label, count] = item.split("#");
        return { label, count: parseInt(count, 10) };
      });

      // Determine the maximum count for scaling the font size
      const maxCount = Math.max(...items.map((i) => i.count));

      // Randomize or position words within slide
      items.forEach((item, index) => {
        const { label, count } = item;

        // Calculate font size based on the count, scaling it between 20 and 100
        const fontSize = 20 + (80 * count) / maxCount;

        // Randomize the position of the text on the slide (adjusted within bounds)
        const leftPosition = Math.random() * (700 - 100) + 50; // Random left position (within slide width)
        const topPosition = Math.random() * (400 - 50) + 50; // Random top position (within slide height)

        // Add the word as a text shape
        const wordShape = slide.shapes.addTextBox(label, {
          left: leftPosition,
          top: topPosition,
          width: 300, // Adjust width as needed
          height: 100, // Adjust height as needed
        });

        // Set properties for the text (font size, color)
        wordShape.textFrame.textRange.font.size = fontSize;
        wordShape.textFrame.textRange.font.color = "#008080"; // Teal color
        wordShape.name = "wordcloud";

        console.log(`Added word: ${label} with font size ${fontSize}`);
      });

      // Synchronize to apply all changes to the slide
      await context.sync();
    });
  } catch (e) {
    console.log("Error received: ", e);
  }
}

