async function addSlideWithMatchingLayout() {
  await PowerPoint.run(async function (context) {
    let selectedSlideIndex = await getSelectedSlideIndex();

    // Decrement the index because the value returned by getSelectedSlideIndex()
    // is 1-based, but SlideCollection.getItemAt() is 0-based.
    const realSlideIndex = selectedSlideIndex - 1;
    const selectedSlide = context.presentation.slides.getItemAt(realSlideIndex).load("slideMaster/id, layout/id");

    await context.sync();
    console.log(selectedSlide.layout.id);
    context.presentation.slides.add({
      slideMasterId: selectedSlide.slideMaster.id,
      layoutId: selectedSlide.layout.id,
    });

    await context.sync();
  });
}

async function insertPollSlide(base64File, reducedindex) {
  await PowerPoint.run(async function (context) {
    // Get the index and ID of the currently selected slide
    let selectedSlideIndex = await getSelectedSlideIndex();
    const realSlideIndex = selectedSlideIndex - reducedindex;
    const selectedSlide = context.presentation.slides.getItemAt(realSlideIndex).load("id");

    await context.sync(); // Sync to retrieve the slide ID

    // Options for inserting the slide
    const insertOptions = {
      formatting: "KeepSourceFormatting", // Keeps the formatting of the inserted slide
      targetSlideId: selectedSlide.id, // Insert the new slide after the currently selected one
    };

    // Insert the poll slide from the Base64 string
    context.presentation.insertSlidesFromBase64(base64File, insertOptions);

    await context.sync();
  });
}

